<!--图书页面的根组件-->
<template>
  <el-container>
    <el-aside style="width: 200px;margin-top: 20px">
      <switch></switch>
      <!-- 加入左侧分类栏; SideMenu组件是LibraryIndex组件的一个 子组件
        1. Vue组件间通信: 这里的ref="sideMenu" 表示我们给SideMenu设置一个引用名,
        可以通过 _this.refs.sideMenu 来引用侧面导航栏的实例，并获取它的 data 了
        2. @indexSelect="listByCategory"，为 listByCategory() 方法设置触发事件
          indexSelect事件定义在SideMenu.vue中 (更详细看步骤(九))
        -->
      <SideMenu @indexSelect="listByCategory" ref="sideMenu"></SideMenu>
    </el-aside>
    <el-main> <!--  中心图书展示区域    -->
      <books class="books-area" ref="booksArea"></books>
    </el-main>
  </el-container>
</template>

<script>
// 引入左侧分类栏 和 中央图书展示区
import SideMenu from './SideMenu'
import Books from './Books'
export default {
  name: 'AppLibrary',
  components: {SideMenu, Books},
  methods: {
    listByCategory () {
      var _this = this
      var cid = this.$refs.sideMenu.cid // 从sideMenu获取类别号
      var url = 'categories/' + cid + '/books' // 构造访问的url
      // 用get方法向后端发送请求, 获取该分类的图书信息 (对应于后端LibraryController中getMapping)
      // 返回成功的话, 替换data中数据, 从而动态渲染
      this.$axios.get(url).then(resp => {
        if (resp && resp.status === 200) {
          // 修改Books.vue组件的data, 渲染图书信息
          _this.$refs.booksArea.books = resp.data
        }
      })
    }

  } // listByCategory
}

</script>

<style scoped>
  .books-area {
    width: 990px;
    margin-left: auto;
    margin-right: auto;
  }
</style>



