<!-- 左侧的图书分类栏
    功能: 点击分类可以引发分类事件 -->
<template>
  <!-- 下面@select="": 表示select时触发handlSelect()定义的事件 -->
  <el-menu
    class="categories"
    default-active="0"
    @select="handleSelect"
  active-text-color="red">
    <el-menu-item index="0">
      <i class="el-icon-menu"></i>
      <span slot="title">全部</span>
    </el-menu-item>
    <el-menu-item index="1">
      <i class="el-icon-menu"></i>
      <span slot="title">童装</span>
    </el-menu-item>
    <el-menu-item index="2">
      <i class="el-icon-menu"></i>
      <span slot="title">女装</span>
    </el-menu-item>
    <el-menu-item index="3">
      <i class="el-icon-menu"></i>
      <span slot="title">上衣</span>
    </el-menu-item>
    <el-menu-item index="4">
      <i class="el-icon-menu"></i>
      <span slot="title">棉服</span>
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  name: 'SideMenu',
  data () {
    return {
      cid: ''
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      this.cid = key // 此处的key是上边分类栏<el-menu>中的index值, 即分类码
      // emit，即触发，在子组件中使用 $emit 方法，即可触发在父组件中定义的事件
      this.$emit('indexSelect') // 此处在LibraryIndex.vue中indexSelect指向ListByCategory()
    }
  } // methods
}

/* 组件间通信 总结
当你通过点击选择侧边导航栏的一个标签后，发生了如下的动人的故事：
1. 触发 <el-menu> 组件的 @select 事件，执行 handleSelect 方法
2. handleSelect 方法触发 indexSelect 事件，并把 key，即 <el-menu-item>标签的index属性的值
   赋给 data 中定义的属性，即分类码
3. 父组件收到指令，执行事件对应的方法，即 listByCategory 方法
4. 发送请求，后端执行查询代码，返回数据，再通过 refs 修改 Books组件的 data 以动态渲染页面
* */

</script>

<style scoped>
  .categories {
    position: fixed;
    margin-left: 50%;
    left: -600px;
    top: 100px;
    width: 150px;
  }
</style>



