<template>
  <div>
    <el-row style="height: 840px;">
      <search-bar @onSearch="searchResult" ref="searchBar"></search-bar>
      <!-- 此处el-tooltip表示图书的页面, 通过v-for定位到item-->
      <el-tooltip effect="dark" placement="right"
                  v-for="item in books.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                  :key="item.id">

        <!--前三个p标签: 是当鼠标移动到其上时显示的信息小页 (mdp:无需改,直接替换) -->
        <p slot="content" style="font-size: 14px;margin-bottom: 6px;">{{item.title}}</p>
        <p slot="content" style="font-size: 13px;margin-bottom: 6px">
          <span>{{item.date}}</span> /
          <span>{{item.press}}</span> /
          <span>{{item.abs}}</span>
        </p>
        <p slot="content" style="width: 300px" class="abstract">{{item.abs}}</p>

        <!-- 下面el-card中2个div分别表示title, author  -->
        <el-card style="width: 435px;margin-bottom: 20px;height: 73px;float: left;margin-right: 15px" class="book"
                 bodyStyle="padding:10px" shadow="hover">
          <!-- 先暂时取消点击修改功能 <div class="info" @click="editBook(item)">-->
          <div class="info">
            <div class="title">
              <a href="">{{item.title}}</a>
            </div>
            <i class="el-icon-delete" @click="deleteBook(item.id)"></i>
          </div>
          <div class="author">{{item.author}}</div>
        </el-card>

      </el-tooltip>

      <!--  修改/增加书籍, 子组件(EditForm.vue)的onSubmit事件 触发loadPapers()执行,即返回所有书籍 -->
      <edit-form @onSubmit="loadBooks()" ref="edit"></edit-form>
    </el-row>
    <!--  最下边的页面栏  -->
    <br>
    <el-row>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pagesize"
        :total="books.length">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
// 把信息修改页 和 搜索栏加入进来
import EditForm from './EditForm'
import SearchBar from './SearchBar'
export default {
  name: 'Books',
  components: {EditForm, SearchBar},
  data () {
    return {
      books: [],
      currentPage: 1,
      pagesize: 17
    }
  },
  /* mounted: 钩子函数，即在某个特定条件下被触发的函数
    钩子函数由于一般与生命周期对应，所以也有 “生命周期钩子函数” 这种叫法
    mounted即“已挂载”,所谓挂载，就是我们写的Vue代码被转换为HTML并替换相应的DOM 结束的时候，
    就会执行 mounted 里面的代码
  */
  mounted: function () {
    this.loadBooks() // loadBooks方法定义在下面
  },
  methods: {
    loadBooks () {
      /* 利用axios发送了一个get请求，在收到后端返回的成功代码后把 data 里的数据替换为后端返回的数据
      利用 data 和 template 里相应元素的双向绑定，实现页面的动态渲染 */
      var _this = this
      this.$axios.get('/books').then(resp => {
        if (resp && resp.status === 200) {
          _this.books = resp.data
        }
      })
    },
    // 处理分页展示的页码
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage
      console.log(this.currentPage)
    },
    // 搜索(搜索框): 由子组件SearchBar中的点击或Enter进行触发, 后端由BookService中的Search功能从数据库获取
    searchResult () {
      var _this = this
      this.$axios
        .post('/search', {
          keywords: this.$refs.searchBar.keywords
        }).then(resp => {
          if (resp && resp.status === 200) {
            _this.books = resp.data
          }
        })
    },
    // 删除操作: 对应后端PostMapping方法
    deleteBook (id) {
      this.$confirm('此操作将永久删除该书籍, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => { // 选择confirm(确定)按钮
        this.$axios // post不能像get请求那样直接把参数写在url里，而需要以键值对的方式传递
          .post('/delete', {id: id}).then(resp => {
            if (resp && resp.status === 200) {
              this.loadBooks() // 删除后重新加载页面
            }
          })
      }
      ).catch(() => { // 选择cancel按钮
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
      // alert(id)
    },
    editBook (item) { // 点击封面后触发的事件: 弹出修改页面
      this.$refs.edit.dialogFormVisible = true
      this.$refs.edit.form = {
        id: item.id,
        cover: item.cover,
        title: item.title,
        author: item.author,
        date: item.date,
        press: item.press,
        abs: item.abs,
        category: {
          id: item.category.id.toString(),
          name: item.category.name
        }
      }
    }
  }
}
</script>

<style scoped>

  .cover {
    width: 115px;
    height: 172px;
    margin-bottom: 7px;
    overflow: hidden;
    cursor: pointer;
  }

  img {
    width: 115px;
    height: 172px;
    /*margin: 0 auto;*/
  }

  .title {
    font-size: 14px;
    text-align: left;
  }

  .author {
    color: #333;
    width: 102px;
    font-size: 13px;
    margin-bottom: 6px;
    text-align: left;
  }

  .abstract {
    display: block;
    line-height: 17px;
  }

  .el-icon-delete {
    cursor: pointer;
    float: right;
  }

  .switch {
    display: flex;
    position: absolute;
    left: 780px;
    top: 25px;
  }

  a {
    text-decoration: none;
  }

  a:link, a:visited, a:focus {
    color: #3377aa;
  }

</style>



