import Vue from 'vue'
import Router from 'vue-router'

// 导入自己编写的组件
import Appindex from '../components/home/Appindex'
import Login from '@/components/Login'
import Home from '@/components/Home'
import LibraryIndex from '../components/library/LibraryIndex'
import ArchiveIndex from '../components/archive/ArchiveIndex'

Vue.use(Router)

export default new Router({
  mode: 'history',
  // 固定写法: 加入页面
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home, // 增加导航栏
      /* 注意我们并没有把首页的访问路径设置为 /home/index，
      仍然可以通过 /index 访问首页，这样配置其实是感受不到 /home 这个路径的存在的。
      之后再添加新的页面，可以直接在 children 中增添对应的内容 */
      redirect: '/index',
      children: [
        {
          path: '/index',
          name: 'AppIndex',
          component: Appindex
        },
        {
          path: '/library',
          name: 'Library',
          component: LibraryIndex
          // 访问此页需要登录验证
          // meta: {
          //   requireAuth: true
          // }
        },
        {
          path: '/archive',
          name: 'Archive',
          component: ArchiveIndex
        }
      ]
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    }
  ]
})



