// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import ElementUI from 'element-ui' // 引入ElementUI前端框架
import 'element-ui/lib/theme-chalk/index.css'

// 设置反向代理，前端请求默认发送到 http://localhost:8443/api
var axios = require('axios')
axios.defaults.baseURL = 'http://localhost:8443/api'

// 全局注册，之后可在其他组件中通过 this.$axios 发送数据
Vue.prototype.$axios = axios

// 阻止vue 在启动时生成生产提示
Vue.config.productionTip = false

Vue.use(ElementUI)

/* eslint-disable no-new */
/* 创建一个新的vue实例 */
new Vue({
  el: '#app', // el 属性提供一个在页面上已存在的DOM元素作为Vue对象的挂载目标
  router, // router代表该对象包含Vue Router，并使用项目中定义的路由
  components: { App }, // components 表示该对象包含的 Vue 组件
  template: '<App/>' // template 是用一个字符串模板作为 Vue 实例的标识使用，类似于定义一个 html 标签
})



